export default "import EmblaCarousel from 'embla-carousel';\nimport { setupTweenScale } from './EmblaCarouselTweenScale';\nimport { addDotBtnsAndClickHandlers } from '../EmblaCarouselDotButton';\nimport { addPrevNextBtnsClickHandlers } from '../EmblaCarouselArrowButtons';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst OPTIONS = {};\n\nconst emblaNode = document.querySelector('.embla');\nconst viewportNode = emblaNode.querySelector('.embla__viewport');\nconst prevBtn = emblaNode.querySelector('.embla__button--prev');\nconst nextBtn = emblaNode.querySelector('.embla__button--next');\nconst dotsNode = document.querySelector('.embla__dots');\n\nconst emblaApi = EmblaCarousel(viewportNode, OPTIONS);\nconst removeTweenScale = setupTweenScale(emblaApi);\n\nconst removePrevNextBtnsClickHandlers = addPrevNextBtnsClickHandlers(emblaApi, prevBtn, nextBtn);\nconst removeDotBtnsAndClickHandlers = addDotBtnsAndClickHandlers(emblaApi, dotsNode);\n\nemblaApi\n    .on('destroy', removeTweenScale)\n    .on('destroy', removePrevNextBtnsClickHandlers)\n    .on('destroy', removeDotBtnsAndClickHandlers);\n";